.btn {
    padding: 12px 16px;
    margin-top: 18px;
    font-size: 14px;
    color: #fff;
    background-color: #3498db;
    border: 0;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color .25s ease-in-out;
  }
  
  .btn:hover {
    background-color: #2980b9;
  }